@font-face {
  font-family: 'my-cv';
  src:  url('asset/font/my-cv.eot?dpdngi');
  src:  url('asset/font/my-cv.eot?dpdngi#iefix') format('embedded-opentype'),
    url('asset/font/my-cv.ttf?dpdngi') format('truetype'),
    url('asset/font/my-cv.woff?dpdngi') format('woff'),
    url('asset/font/my-cv.svg?dpdngi#my-cv') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="my-cv-"], [class*=" my-cv-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'my-cv' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

